<template>
  <div id="app">
    <graph-view class='visualization' :graph='currentGraph' @selected='onVideoSelected'></graph-view>
    <form id='searchForm' @submit.prevent='searchFormSubmitHandler'>
        <input type='text' id='searchString' placeholder='Enter YouTube search query' autofocus v-model='searchString'/>
        <input type='submit' id='startSearch' title='Start visualization' value='Show Graph' />
    </form>

    <div id='log'></div>
    <video-preview :videoId='selectedVideo' @close='closeVideo'></video-preview>
    <div class='footer'>
        <a @click.prevent='showAbout = true' class='about-link'>About</a>
    </div>
    <div id='copyText'>(C) 2012 Andrei Kashcha</div>


    <div id='about' v-if='showAbout'>
        <h2>About Yasiv</h2>
        <div style='text-align: left'>
            <p>This web site shows related videos from YouTube.</p>
            <p>Enter a search term in the box above, and press the "Show Graph" button.</p>
            <p>Each icon is a YouTube video. Each connection means that videos are related (according to YouTube).</p>
            <p><a href='https://twitter.com/anvaka'>Andrei Kashcha</a></p>
        </div>
        <ul>
            <li><a href="https://github.com/anvaka/yasiv-youtube">GitHub</a></li>
            <li><a href="https://twitter.com/#!/yasivcom">Twitter</a></li>
            <li><a href="http://www.facebook.com/pages/Yasiv/234739739945193">Facebook</a></li>
            <li><a href='mailto:anvaka@yasiv.com'>Contact</a></li>
        </ul>
        <a href='#' @click.prevent='showAbout = false'  class='primary-action close-about' title='Close this message'>Close</a>
    </div>
  </div>
</template>

<script>
import queryState from 'query-state';
import GraphView from './components/GraphView.vue';
import VideoPreview from './components/VideoPreview.vue';

import { getVideoIdFromUrl } from './lib/utils.js';
import { buildGraphForSearchTerm } from './lib/graphBuilder.js';

const qs = queryState();

export default {
  name: 'app',
  data() {
    const searchString = qs.get('q') || '';

    return {
      request: null,
      logMessage: '',
      searchString,
      showAbout: searchString.length === 0,
      selectedVideo: null,
    };
  },

  created() {
    qs.onChange(this.onQueryChange, this);
    this.startSearch();
  },

  computed: {
    currentGraph() {
      if (this.request) {
        return this.request.graph;
      }
    },
  },

  methods: {
    closeVideo() {
      this.selectedVideo = null;
    },
    searchFormSubmitHandler() {
      qs.set({ q: this.searchString });
      this.startSearch();
    },

    onVideoSelected(videoId) {
      this.selectedVideo = videoId;
    },

    onQueryChange() {
      this.searchString = qs.get('q');
      this.startSearch();
    },

    startSearch() {
      const q = qs.get('q');
      if (!q) return;

      if (this.request) {
        this.request.progress.cancel();
        // TODO: Clean the scene.
      }
      this.showAbout = false;

      this.request = buildGraphForSearchTerm(q);
    }
  },

  components: {
    GraphView,
    VideoPreview
  }
};
</script>

<style>
*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    font-size: 100%;
}
body {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;

  /* font info has to be applied to body so it applies to mouseovers and date picker */
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.window {
  left: 8px;
  top: 64px;
  width: 420px;
  height: 290px; /* 248 - is golden ratio of 420 + 42px for the header */
  box-shadow: 0 2px 4px rgba(0,0,0,.2), 0 -1px 0 rgba(0,0,0,.02);
}
a.primary-action {
  color: #ff4081;
}
a {
  color: #4183C4;
  text-decoration: none;
}
a:hover {
  color: #ff4081;
}

.visualization {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#searchForm {
  padding: 14px;
  position: absolute;
  white-space: nowrap;
  background: white;
  box-shadow: 0 2px 4px rgba(0,0,0,.2), 0 -1px 0 rgba(0,0,0,.02);
  top: 8px;
  left: 8px;
  width: 420px;
  display: flex;
}
#searchString {
  height: 28px;
  padding-left: 10px;
  font-size: 18px;
  outline: none;
  border: none;
  flex: 1;
}

#searchString:focus {
  border: none;
}

#startSearch {
  color: #4183C4;
  background: transparent;
  border: none;
  padding: 0;
  font-size: 14px;
  cursor: pointer;
  margin-left: 4px;
}

#startSearch:hover {
  color: #ff4081;
}


#header {
    text-align: center;
    background: #6DAEE1;
    position: absolute;
    left: 0;
    width: 100%;
    height: 28px;
    color : white;
}

.footer {
  position: absolute;
  width: 100%;
  bottom : 9px;
  display: flex;
  justify-content: center;
}

#copyText {
  position: absolute;
  left: 10px;
  bottom: 9px;
  font-size: 9px;
  color: #999;
}

#about {
  position: absolute;
  left: 8px;
  padding: 14px;
  top: 64px;
  width: 420px;
  background: white;
  box-shadow: 0 2px 4px rgba(0,0,0,.2), 0 -1px 0 rgba(0,0,0,.02);
}
.about-link {
  background: rgba(255, 255, 255, 0.8);
  padding: 0 14px;
}
.close-about {
  float: right;
}

@media only screen and (max-width: 600px) {
  #searchForm {
    width: 100%;
    top: 0;
    left: 0;
  }
  .window {
    left: 0;
    top: 56px;
    height: 35vh;
    width: 100%;
  }
  #searchString {
    font-size: 14px;
  }

  #about {
    left: 0;
    top: 56px;
    width: 100%;
  }

  .footer {
    padding-right: 14px;
    justify-content: flex-end;
  }
}
</style>



